<template>
  <div id="favourite">
    <p class="tit">猜你喜欢</p>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        @load="onLoad"
      >

        <ul class="list">
          <li v-for="(item, index) in favorList" :key="index">
            <div class="left" @click="$router.push({
                                                path: detailURL,
                                                query:{id: item.id}
                                              })">

              <img v-lazy="getImgURL(item.showImageUrl.thumbnailPelativePath)">
            </div>

            <div class="right">
              <h3 class="text-overflow"
                  @click="$router.push({
                                    path: detailURL,
                                    query:{id: item.id}
                                  })">
                {{ item.name }}
              </h3>
              <p>{{ timeStamp(item.publishTime) }}</p>
            </div>
          </li>
        </ul>

      </van-list>
      <div class="no_more" v-show="finished">没有更多了......</div>
    </van-pull-refresh>

  </div>
</template>

<script>
    export default {

      data(){
        return{
          detailURL: '/videoDetail',
          favorList: [],

          loading: false,
          finished: false,
          isLoading: false,
          pageNum: 1, // 页数
          offset: 0,  // 下次加载起始页
          totalPage: 0 // 总页数
        }
      },

      methods:{

        async init(){
          let that = this;

          let params = {
            offset: (that.pageNum - 1) * 8
          };

          let id = this.$route.query.id;
          let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/videoList', params);
          console.log(res);
          that.favorList = res.result.videosAll.data;
        },

        async concatData(){
          let that = this;
          that.pageNum += 1;

          // 数据全部加载完成
          let params = {
            offset: (that.pageNum - 1) * 8
          };

          let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/videoList', params);

          that.favorList = that.favorList.concat(res.result.videosAll.data);
          this.loading = false;

          if(this.pageNum >= this.totalPage){
            this.finished = true; // 加载结束
          }
        },

        onRefresh() {
          setTimeout(() => {
            this.$toast({
              message: '刷新成功',
              position: 'bottom'
            });
            this.isLoading = false;
            this.pageNum = 1;
            this.loading = false;
            this.finished = false;
            this.isLoading = false;
            this.init();
          }, 500);
        },

        onLoad() {
          // 异步更新数据
          setTimeout(() => {
            this.concatData();
          }, 500);
        }
      },

      created(){
        this.init();
      }
    }
</script>

<style scoped lang="scss">

  .no_more{
    font-size: 30px;
    color: #8d8d8d;
    text-align: center;
    margin: 40px auto 20px;
  }

  #favourite{
    background: #FFF;
    padding: 30px;

    .tit{
      font-size: 32px;
      color: #6EAFFF;
    }

    .list{
      margin-top: 30px;

      li{
        height: 150px;
        display: flex;
        margin-bottom: 25px;
        padding-bottom: 25px;
        &:not(:last-child){
          border-bottom: 5px solid #F5F5F5;
        }

        .left{
          flex: .5;
          padding-right: 30px;
        }

        .right{
          min-width: 0;
          position: relative;
          flex: 1;

          h3{
            font-size: 30px;
            color: #333;
          }

          p{
            color: #8D8D8D;
            font-size: 28px;
            position: absolute;
            bottom: 0;
          }
        }
      }
    }
  }
</style>



